<template>
  <v-container grid-list-xl text-xs-center>
  <v-layout row wrap>
    <v-flex xs6 mt-4>
    <v-card>
      <v-card-media
      <a class="twitter-timeline"
      height = "400px"
      href="https://twitter.com/hashtag/vet"
      data-widget-id="984191781695877121">#vet Tweets</a>
  </v-card-media>
  </v-card>
</v-flex>
    <v-flex xs6 mt-4>
      <v-card>
        <v-card-media
          class="white--text"
          height="400px"
          src="/static/cowfield.jpg">
        <v-btn right class="white--text" flat to="/ViewCheckups">View Checkups</v-btn>
</v-card-media>
</v-card>
    </v-flex>
  </v-layout>
</v-container>
</template>

<script>
!(function (d, s, id) {
  var js
  var fjs = d.getElementsByTagName(s)[0]
  var p = / ^ http:/.test(d.location) ? 'http' : 'https'
  if (!d.getElementById(id)) {
    js = d.createElement(s)
    js.id = id
    js.src = p + '://platform.twitter.com/widgets.js'
    fjs.parentNode.insertBefore(js, fjs)
  }
})(document, 'script', 'twitter-wjs')
</script>



